define('tiny/utils/array_all_succeeded', ['exports'], function (exports) {

  'use strict';

  exports['default'] = arrayAllSucceeded;

  function arrayAllSucceeded(array) {
    var success = true;
    array.forEach(function (record) {
      if (record.state == 'rejected') {
        success = false;
      }
    });
    return success;
  }

});